import { mapState, mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'
import InputSeek from '../../../components/inputSeekTemplate'
import myCourseList from '../../../components/myCourseListTemplate'
import LoadingMore from '../../../components/loadingMoreTemplate'

export default {
  data () {
    return {
      upyunUrl,
      keywords: '',
      page: 1
    }
  },
  onLoad () {
    this.page = 1
    this.getMyCourseListFn(true)
  },
  computed: {
    ...mapState([
      'userInfo',
      'myCourseList'
    ])
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.page = 1
    this.getMyCourseListFn(true)
  },
  // 上拉加载
  onReachBottom () {
    this.page += 1
    this.getMyCourseListFn(false)
  },
  methods: {
    ...mapMutations({
      setMyCourseList: 'SET_MY_COURSE_LIST'
    }),
    ...mapActions([
      'getIsReview',
      'getMyCourseList'
    ]),
    // 获取我的课程列表
    getMyCourseListFn (loadingStatus) {
      const data = {
        that: this,
        keywords: this.keywords,
        page: this.page
      }
      if (loadingStatus) {
        uni.showLoading({ title: 'Loading' })
        this.setMyCourseList([])
      }
      uni.showNavigationBarLoading()
      this.getMyCourseList(data).then(() => {
        if (loadingStatus) uni.hideLoading()
        uni.stopPullDownRefresh()
        uni.hideNavigationBarLoading()
      })
    },
    // 回车搜索
    onEnterFn () {
      this.page = 1
      this.getMyCourseListFn(true)
    },
    // 一件清空关闭list页面
    onEmptyDataFn () {
      this.page = 1
      this.keywords = ''
      this.getMyCourseListFn(true)
    }
  },
  components: {
    InputSeek,
    myCourseList,
    LoadingMore
  }
}
